import React, { Component } from "react";

class NewsFeed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoaded: false,
      articles: []
    };
  }

  //Client key ID - 639c8ec2-dbc7-4c19-b078-fa83a44aacfe
  //Client key secret - HUD9cGNjjFX7bbLVH245UG9TJdasbB2BpB+TdVlo4fM=

  //"https://newsapi.org/v2/top-headlines?country=za&apiKey=ef67bbc888aa460fad925c0383d2ff0b"

  componentDidMount() {
    this.fetchData();
  }

  fetchData() {
    fetch(
      "https://newsapi.org/v2/top-headlines?country=za&apiKey=ef67bbc888aa460fad925c0383d2ff0b"
    )
      .then(response => response.json())
      .then(parsedJSON => {
        console.log(parsedJSON);
        this.setState({
          isLoaded: true,
          articles: parsedJSON.articles
        });
      });
    // fetch("http://feeds.news24.com/articles/news24/MyNews24/rss")
    //   .then(response => response.text())
    //   .then(str => new window.DOMParser().parseFromString(str, "text/xml"))
    //   .then(data => {
    //     for (var i = 0; i < data.getElementsByTagName("item").length; i++) {
    //       for (var x = 0; x < data.getElementsByTagName("title").length; x++)
    //         console.log(
    //           data.getElementsByTagName("title")[x + 1].childNodes[0]
    //             .nodeValue +
    //             "\n" +
    //             data.getElementsByTagName("description")[x].childNodes[0]
    //               .nodeValue
    //         );
    //     }
    //   })
    //   .catch(error => console.log("parsing failed", error));
  }

  render() {
    const { error, isLoaded, articles } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <div>Loading...</div>;
    } else {
      return (
        <ul>
          {articles.map(article => (
            <li key={article.title}>
              {article.title} {article.description}
              {article.content}
            </li>
          ))}
        </ul>
      );
    }
  }
}

export default NewsFeed;
